.bookingsPage {
  padding: 60px 20px;
}

.bookings__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

.booking {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  gap: 10px 40px;
  width: 100%;
  padding: 15px 25px;
  background-color: var(--color-blue-100);
  border-left: 4px solid var(--color-blue-200);
  border-radius: 1px;
}

.booking__title {
  width: 100%;
  margin: 0;
  font-weight: 600;
  font-size: var(--font-size-400);
  font-family: 'Playfair Display', serif;
  line-height: 1;
}

.booking__cancel {
  position: absolute;
  top: 10px;
  right: 15px;
  padding: 0;
  font-weight: 300;
  font-size: var(--font-size-400);
  line-height: 1;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  opacity: 0.5;
}

.visuallyHidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(50%);
}