.tripInfo{width:100%;max-width:300px;padding-bottom:10px;border-bottom:1px solid var(--color-gray)}.tripInfo__title{margin:0 0 5px;font-weight:600;font-size:var(--font-size-400);font-family:"Playfair Display",serif}.tripInfo__content{display:flex;font-weight:300}.tripInfo__duration{min-width:120px;padding-left:25px;background-repeat:no-repeat;background-position:0 50%;background-size:15px}.tripInfo__level::before{content:"•";margin-right:10px;color:var(--color-blue-200)}