.footer {
  padding: 30px 20px;
  color: var(--color-white);
  text-align: center;
  background-color: var(--color-blue-400);
}

.footer__text {
  display: flex;
  justify-content: center;
  gap: 7px;
}

.footer__icon {
  width: 16px;
}

.footer__link {
  color: var(--color-blue-200);
  text-decoration: none;
  transition: 0.1s color ease;

  &:hover {
    color: var(--color-blue-300);
  }
}