.tripCard{display:flex;flex-direction:column;max-width:320px;background-color:var(--color-white);border-radius:1px;box-shadow:var(--shadow)}.tripCard__content{display:flex;flex-direction:column;gap:15px;padding:20px 30px}.tripInfo{width:100%;max-width:300px;padding-bottom:10px;border-bottom:1px solid var(--color-gray)}.tripInfo__title{margin:0 0 5px;font-weight:600;font-size:var(--font-size-400);font-family:"Playfair Display",serif}.tripInfo__content{display:flex;font-weight:300}.tripInfo__duration{min-width:120px;padding-left:25px;background-repeat:no-repeat;background-position:0 50%;background-size:15px}.tripInfo__level::before{content:"•";margin-right:10px;color:var(--color-blue-200)}.tripPrice__value{display:block;color:var(--color-blue-200);font-size:var(--font-size-600);line-height:1}.button{display:block;width:100%;min-height:45px;padding:10px 15px;color:var(--color-white);font-size:var(--font-size-200);text-align:center;text-decoration:none;background-color:var(--color-blue-200);border:none;border-radius:1px;cursor:pointer;transition:background-color .2s ease}.button:hover{background-color:var(--color-blue-300)}.tripCard__img{height:210px}